import React, {useEffect, useState} from "react";
import ReactPlayer from "react-player";
import {Helmet} from "react-helmet-async";
import Spinner from "../../components/Spinner/Spinner";
import "./Events.scss";

interface IdType {
  kind: string;
  videoId: string;
}
interface ResponseDataType {
  id: IdType;
}
const Events: React.FC = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [videos, setVideos] = useState<ResponseDataType[]>([]);
  useEffect(() => {
    console.log("use Effect");
    const fetchYoutubeVideos = async () => {
      const response = await fetch(
        "https://www.googleapis.com/youtube/v3/search?key=AIzaSyCrMbcOpu4YQctoxNr5lllzButaJjuNADw&channelId=UCfO4c84YkrDPppGktKq-WRA&part=snippet,id&order=date&maxResults=20",
      );
      const data = await response.json();
      setVideos(data.items);
    };
    fetchYoutubeVideos();
    setIsLoading(false);
  }, []);
  return (
    <>
      <Helmet>
        <title>Events - Kerala Samajam Hamburg</title>
        <meta property="og:title" content={"Events - Kerala Samajam Hamburg"} />
      </Helmet>
      {isLoading && (
        <div className="spinner-overlay">
          <Spinner />
        </div>
      )}
      {videos?.map((element, index) => {
        if (element.id.videoId !== undefined) {
          return (
            <div className="wrapper" key={index}>
              <ReactPlayer
                url={`https://www.youtube.com/watch?v=${element.id.videoId}&ab_channel=KeralaSamajamHamburg`}
              />
            </div>
          );
        }
      })}
    </>
  );
};
export default Events;
